import React from 'react'




function Element(prop: any) {

	function chooseCurrency() {
		if (prop.doesUsd) return <p className='list__element__price'> {prop.current_price}z</p>
		else return <p className='list__element__price'> ${prop.current_price}</p>
	}

	return (
		<div className='list__element' id={prop.id}>
			<img className='list__element__img' src={prop.image}></img>
			<h3 className='list__element__name'> {prop.id}</h3>
			{chooseCurrency()}
		</div>
	)
}

export default Element
