import React, { BaseSyntheticEvent, ReactElement, ReactFragment } from 'react'

interface Prop {
	showElement: (event: BaseSyntheticEvent) => void
	elements: any[]
}

function CryptoList(prop: Prop) {
	return (
		<div onClick={prop.showElement} className='list'>
			{prop.elements}
		</div>
	)
}
export default CryptoList
