import React, { BaseSyntheticEvent } from 'react'
import WelcomeScreen from './WelcomeScreen'
import CryptoScreen from './CryptoScreen'

interface Prop {
	daysInGraph: string
	changeDaysInGraph: (event: BaseSyntheticEvent) => void
	doesUsd: boolean
	changeCurrency: () => void
	informations: any
	graphData: any
}

function ActualCrypto(prop: Prop) {
	if (prop.informations == false || prop.graphData == false || prop.informations == undefined) {
		return <WelcomeScreen />
	} else {
		return (
			<CryptoScreen
				daysInGraph={prop.daysInGraph}
				changeDaysInGraph={prop.changeDaysInGraph}
				doesUsd={prop.doesUsd}
				changeCurrency={prop.changeCurrency}
				informations={prop.informations}
				graphData={prop.graphData}
			/>
		)
	}
}
export default ActualCrypto
