import React, { BaseSyntheticEvent } from 'react'

interface Props {
	moveCircle: (event: BaseSyntheticEvent) => void
}

function CurrencyChanger(props: Props) {
	return (
		<div onClick={props.moveCircle} className='actual__currencyChanger'>
			<div className='actual__currencyChanger__circle'></div>
			<p className='actual__currencyChanger__PLN'>PLN</p>
			<p className='actual__currencyChanger__USD'>USD</p>
		</div>
	)
}

export default CurrencyChanger
