import React from 'react'
import { Line } from 'react-chartjs-2'
import Chart from 'chart.js/auto'



function Graph(prop) {
	// console.log(`BRUH:${prop.data}`)
	console.log(prop.daysInGraph)
	Chart.defaults.color = '#fff'
	return (
		<div className='actual__graph'>
			<Line
				datasetIdKey='id'
				data={{
					labels: prop.data.map(item =>
						prop.daysInGraph != '1' ? item[0].toLocaleDateString('en-US') : item[0].getHours()
					),
					datasets: [
						{
							label: 'price',
							data: prop.data.map(item => item[1]),
							backgroundColor: '#ccc',
							borderColor: '#fff',
							fillColor: '#fffc',
							borderWidth: 2,
							color: '#fff',
							pointStrokeColor: '#fff',
						},
					],
				}}
			/>
		</div>
	)
}
export default Graph
