import React, { BaseSyntheticEvent } from 'react'
import CryptoList from './components/CryptoList'
import CryptoData from './components/LoadCryptoData'
import ActualData from './components/LoadGraphData'
import Element from './components/CryptoListElement'
import ActualCrypto from './components/ActualCrypto'
import Searchbar from './components/SearchBar'
import { DaysOnGraph } from './components/interfaces/interfaces'
function App() {
	const [usdToPln, setUsdToPln] = React.useState(false)
	const [actualCrypto, setActualCrypto] = React.useState<object>()
	const [actualCryptoId, setActualCryptoId] = React.useState<string>('bitcoin')
	const [searchedText, setSearchedText] = React.useState<string>('')

	const [daysInGraph, setDaysInGraph] = React.useState<DaysOnGraph>(DaysOnGraph.day)
	function changeDaysInGraph(event: BaseSyntheticEvent) {
		setDaysInGraph(event.target.value)
	}
	function handleChange(event: BaseSyntheticEvent) {
		console.log(event.target.value)
		setSearchedText(event.target.value)
	}

	function changeCurrency() {
		setUsdToPln(!usdToPln)
	}

	let cryptoData = CryptoData(usdToPln, searchedText)
	console.log(cryptoData)
	const readyData: object[] = cryptoData.map(item => (
		<Element
			doesUsd={usdToPln}
			key={item.key}
			image={item.image}
			id={item.id}
			current_price={item.current_price}></Element>
	))

	function showElement(event: BaseSyntheticEvent) {
		setActualCryptoId(event.target.id)
		for (let i = 0; i < cryptoData.length; i++) {
			if (cryptoData[i].id == event.target.id) setActualCrypto(cryptoData[i])
		}
	}

	const dataToCrypto = () => {
		for (let i = 0; i < cryptoData.length; i++) {
			if (cryptoData[i].id == actualCryptoId) {
				return cryptoData[i]
			}
		}
		return actualCrypto
	}
	const dataToGraph = ActualData(actualCryptoId, usdToPln, daysInGraph)
	console.log(typeof readyData)
	console.log(readyData)
	return (
		<div className='app'>
			<Searchbar handleChange={handleChange} />
			<CryptoList showElement={showElement} elements={readyData} />

			<ActualCrypto
				daysInGraph={daysInGraph}
				changeDaysInGraph={changeDaysInGraph}
				doesUsd={usdToPln}
				changeCurrency={changeCurrency}
				informations={actualCrypto == undefined ? false : dataToCrypto()}
				graphData={dataToGraph == undefined ? false : dataToGraph}></ActualCrypto>
		</div>
	)
}

export default App
