import React from 'react'

function ActualData(prop: string, doesUsd: boolean, actualDay: any) {
	const URL: string = `https://api.coingecko.com/api/v3/coins/${prop}/market_chart?vs_currency=${
		doesUsd ? 'pln' : 'usd'
	}&days=${actualDay}&interval=${actualDay == '1' ? 'hourly' : 'daily'}`
	console.log('Do wykresu:')
	console.log(URL)
	const [actualData, setActualData] = React.useState<any[]>([])
	React.useEffect(() => {
		fetch(URL)
			.then(res => res.json())
			.then(res => {
				setActualData(res.prices)

				setActualData(prev =>
					prev.map(item => {
						return [new Date(item[0]), item[1]]
					})
				)
			})
	}, [prop, doesUsd, actualDay])

	return actualData
}
export default ActualData
