import React from 'react'
import Currency from './interfaces/interfaces'
function Data(doesUsd: boolean, searchedText: string) {
	const [cryptoData, setCryptoData] = React.useState<Currency[]>([])

	const URL: string = `https://api.coingecko.com/api/v3/coins/markets?vs_currency=${
		doesUsd ? 'pln' : 'usd'
	}&order=market_cap_desc&per_page=${5}&page=1&sparkline=false`
	console.log('wartosc do lewego:')
	console.log(URL)
	function loadCryptoData(res: any, length: number) {
		for (let i = 0; i < length; i++) {
			if (res[i].id.includes(searchedText) || res[i].name.includes(searchedText) || searchedText.length == 0) {
				setCryptoData(prev => {
					return [
						...prev,
						{
							key: i,
							id: res[i].id,
							symbol: res[i].symbol,
							name: res[i].name,
							image: res[i].image,
							current_price: res[i].current_price,
							price_change: res[i].price_change_24h,
							price_change_percentage: res[i].price_change_percentage_24h,
						},
					]
				})
			}
		}
	}

	React.useEffect(() => {
		fetch(URL)
			.then(res => res.json())
			.then(res => {
				setCryptoData([])
				loadCryptoData(res, res.length)
				// setCryptoList(
				// 	cryptoData.map(item => <Element id={item.id} image={item.image} current_price={item.current_price}></Element>)
				// )
			})
	}, [doesUsd, searchedText])

	return cryptoData
}

export default Data
