import React, { BaseSyntheticEvent } from 'react'

interface Prop {
	handleChange: (event: BaseSyntheticEvent) => void
}
function Searchbar(prop: Prop) {
	return (
		<div className='searchBar'>
			<input onChange={prop.handleChange} placeholder='search crypto' className='searchBar__searchInput'></input>
			<i className=' searchBar__searchIcon fa-solid fa-magnifying-glass'></i>
		</div>
	)
}
export default Searchbar
