import React, { BaseSyntheticEvent } from 'react'
import { JsxAttribute } from 'typescript'
import Graph from './CryptoScreenElements/Graph'
import TimelineButtons from './CryptoScreenElements/TimelineButtons'
import Changes from './CryptoScreenElements/Changes'
import CurrencyChanger from './CryptoScreenElements/CurrencyChanger'

interface Prop {
	daysInGraph: string
	changeDaysInGraph: (event: BaseSyntheticEvent) => void
	doesUsd: boolean
	changeCurrency: () => void
	informations: any
	graphData: any
}

function ActualScryptoScreen(prop: Prop) {
	function moveCircle(event: BaseSyntheticEvent) {
		const circle = event.target.children[0]

		circle.classList.toggle('moveUp')
		{
			prop.changeCurrency()
		}
	}

	function priceWithCurrency(x: number) {
		if (prop.doesUsd) {
			return `${x}z`
		} else {
			return ` $${x}`
		}
	}
	console.log(prop.informations)
	console.log(typeof prop.informations)
	return (
		<div className='actual'>
			<div className='actual__logo'>
				<img src={prop.informations.image} className='actual__logo__img' />
				<h2 className='actual__logo__name'> {prop.informations.name}</h2>
			</div>
			<div className='actual__current'>
				<h3>Actual price:</h3>
				<p>{priceWithCurrency(prop.informations.current_price)}</p>
			</div>
			<Changes priceWithCurrency={priceWithCurrency} informations={prop.informations} doesUsd={prop.doesUsd} />
			<TimelineButtons daysInGraph={prop.daysInGraph} changeDaysInGraph={prop.changeDaysInGraph} />
			<CurrencyChanger moveCircle={moveCircle} />

			<Graph daysInGraph={prop.daysInGraph} data={prop.graphData} />
		</div>
	)
}

export default ActualScryptoScreen
