import React, { BaseSyntheticEvent } from 'react'
import PropTypes from 'prop-types'
import { DaysOnGraph } from '../interfaces/interfaces'

interface Props {
	changeDaysInGraph: (event: BaseSyntheticEvent) => void
	daysInGraph: string
}

function TimelineButtons(prop: Props) {
	console.log(typeof prop)

	let buttons = [
		<button
			key='1'
			onClick={prop.changeDaysInGraph}
			value={DaysOnGraph.month}
			className='actual__dateChanger__changeBtn'>
			1 month
		</button>,
		<button
			onClick={prop.changeDaysInGraph}
			value={DaysOnGraph.twoWeeks}
			className='actual__dateChanger__changeBtn'>
			2 weeks
		</button>,
		<button onClick={prop.changeDaysInGraph} value={DaysOnGraph.oneWeek} className='actual__dateChanger__changeBtn'>
			1 week
		</button>,
		<button onClick={prop.changeDaysInGraph} value={DaysOnGraph.day} className='actual__dateChanger__changeBtn'>
			1 day
		</button>,
	]
	for (let i = 0; i < 4; i++) {
		console.log(buttons[i])
		if (buttons[i].props.value == prop.daysInGraph) {
			buttons[i] = (
				<button
					key={buttons[i].props.key}
					onClick={prop.changeDaysInGraph}
					value={buttons[i].props.value}
					className='actual__dateChanger__changeBtn actual__dateChanger__changeBtn--active'>
					{buttons[i].props.children}
				</button>
			)
		} else {
			<button
				key={buttons[i].props.key}
				onClick={prop.changeDaysInGraph}
				value={buttons[i].props.value}
				className='actual__dateChanger__changeBtn'>
				{buttons[i].props.children}
			</button>
		}
	}
	return <div className='actual__dateChanger'>{buttons}</div>
}

export default TimelineButtons
