import React from 'react'
import Currency from '../interfaces/interfaces'
interface Props {
	priceWithCurrency: (x: number) => string
	informations: Currency
	doesUsd: boolean
}
function Changes(prop: Props) {
	const decreaseStyle = {
		color: '#D0342C',
	}
	const increaseStyle = {
		color: '#9FE2BF',
	}

	return (
		<div className='actual__change'>
			<h3> Last changes (24h):</h3>
			<div style={prop.informations.price_change > 0 ? increaseStyle : decreaseStyle} className='actual__change__data'>
				{prop.informations.price_change > 0 ? (
					<i className='fa-solid fa-arrow-up'></i>
				) : (
					<i className='fa-solid fa-arrow-down'></i>
				)}
				<div>
					<p className='actual__change__currency'>
						{' '}
						{prop.priceWithCurrency(Math.ceil(prop.informations.price_change * 1000) / 1000)}
					</p>
					<p className='actual__change__percent'> {prop.informations.price_change_percentage}%</p>
				</div>
			</div>
		</div>
	)
}

export default Changes
